import React, {useState} from 'react'
import {Flex} from '@chakra-ui/core';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button'
import makeStyles from '@material-ui/core/styles/makeStyles';
import TextField from '@material-ui/core/TextField';
import emailjs from 'emailjs-com';
import apiKeys from '../utils/api_keys'
import Map from './Map'

import Alert from '@material-ui/lab/Alert';


const useStyle = makeStyles({
    root: {
        width: '35%',
        padding: '1.5rem'
    },
    contact_form: {
        display: 'flex',
        flexDirection: 'column',
        width: ''
    },
    textfield: {
        marginBottom: '10px'
    },
    submit_button: {
        backgroundColor: 'rgb(77,204,77)',
        color: 'white',
        marginTop: '10px',
        '&:hover': {
            color: 'black'
        },
    },
    
})

function ContactUs() {
    
    const [makeAlert, setAlert] = useState(false);
    const [severity, setSeverity] = useState('');
    const [alertContent, setAlertContent] = useState('');
    const classes = useStyle();

    const sendEmail = (event) => {
        event.preventDefault();
        console.log('event ', event.target)
        const template_params = {
            from: `${event.target['standard-first-name'].value} ${event.target['standard-last-name'].value}` ,
            Subject: event.target['standard-subject'].value ,
            message:event.target['standard-message'].value ,
            email: event.target['standard-email'].value,
        }
        emailjs.send(apiKeys.service_ID,apiKeys.TEMPLATE_ID, template_params, apiKeys.USER_ID)
    .then(function(response) {
       setAlert(true); setSeverity('success'); setAlertContent('Your Request has been set successfully!');
       event.target.reset();

    }, function(err) {
        setAlert(true); setSeverity('error'); setAlertContent('We are not able to send request at this time. Please try again later!')
    })
    .finally(
        setTimeout(() => {
            setAlert(false)
           },5000)
    );
    }

    return (
        <Flex
        height = '94vh'
        bg = 'rgb(240,242,245)'
        justify = 'space-around'
        align = 'center'
        overflowY= 'scroll'
        >
            <Paper className = {classes.root} elevation = {5}>
                {/* <Typography className = {classes.contact_header}>
                    Contact US
                </Typography> */}
                {makeAlert && <Alert severity={severity}>{alertContent}</Alert>}
                <Flex
                direction = 'column'
                justify = 'center'
                padding = '0% 15%'
                wrap = 'wrap'
                mt = '20px'
                >
                <form className = {classes.contact_form} validate = 'true' autoComplete = 'off' onSubmit = {sendEmail}>
                    <TextField
                    id="standard-first-name"
                    label="First Name"
                    helperText = 'Please enter your First Name'
                    className = {classes.textfield}
                    required
                    />
                    <TextField
                    id="standard-last-name"
                    label="Last Name"
                    helperText = 'Please enter your Last Name'
                    className = {classes.textfield}
                    required
                    />

                    <TextField
                    id="standard-subject"
                    label="Subject"
                    helperText = 'Please enter your Subject of the Message'
                    className = {classes.textfield}
                    required
                    />
                    <TextField
                    id="standard-email"
                    label="Email"
                    helperText = 'Please enter your Email'
                    className = {classes.textfield}
                    required
                    />
                    <TextField
                    id="standard-message"
                    label="Message"
                    multiline
                    rows = {5}
                    helperText = 'Please enter your Message'
                    className = {classes.textfield}
                    required
                    />
                    <Button className = {classes.submit_button} type = 'submit'>
                        Submit
                    </Button>
                </form>
                </Flex>

            </Paper>
            <Map />
        </Flex>
    )
}

export default ContactUs
