import React from 'react'
import {Flex} from '@chakra-ui/core'
import {Paper, Typography, makeStyles} from '@material-ui/core'
import AboutImage from '../images/AboutUS.jpg'

const useStyles = makeStyles({
    main_paper: {
        opacity: 0.9,
        padding: '1.5rem',
        width: '75%',
        margin: 'auto'

    },
    main_content: {
        opacity: 1,
        fontSize: '1.5rem'

    }
})

function AboutUS() {
    const classes = useStyles();
    return (
        <Flex 
        width = '100%' 
        height = '95vh'
        background = {`url(/images/AboutUS.jpg) no-repeat center center`}   
        backgroundSize = 'cover'
        backgroundAttachment = 'fixed'
        color = 'white'
        alignItems = 'center'
        justifyContent = 'center'
        overflowY = 'scroll'     
        >
            <Paper className = {classes.main_paper} elevation = {10}>
            <Typography>
            <b>qualiTEAS Inc. envisions next-generation corrosion inspection solutions, to empower clients strengthen their asset management practices and enhance productivity.</b>
            </Typography>
            <br/>
            <Typography varient='paragraph'>
                qualiTEAS is an engineering service company and was founded in 2018, by a group of science graduates from the Memorial University (St. John’s, NL). Given their background, they identified that the dearth of a chemical understanding perspective to corrosion inspection in asset integrity maintenance has always been an issue in industries and attempted to address it through a unique chemical approach. So, keeping in line with that objective, qualiTEAS successfully validated its research on optical spectroscopic techniques to be used for corrosion inspection which can more precisely unveil a corrosion mechanism and its furthe
            </Typography>
            <br/>
            <Typography varient='paragraph'>
                With time we have grown to extend corrosion and subsea analysis services to some major operators in the Canadian Oil and Gas sectors and are consistently undertaking research and development activities, aimed at improving our solutions. However, we have not stopped at that and are working towards digitizing more elements of our work that will ultimately benefit the supply chain. Our present research and development have generated interest both among our clients and other companies (locally and abroad). 
            </Typography>
            <br />
            <Typography varient='paragraph'>
                We are currently developing a software that will feature near-real-time 3D image reconstruction capability, from live videos taken during asset inspections, comprising clearer 3D visuals and analytical tools that will immensely complement asset inspections. In addition, a machine learning based subsea asset image analysis software is also under development that will be capable of rapidly detecting any corrosion, cracks, and pit formations on subsea assets, which in turn will significantly complement predictive maintenance practices in the industry.
            </Typography>
            <br />
            <Typography varient='paragraph'>
                It is our constant endeavour to offer fast and efficient services to our clients, and in the process ensure personnel safety and environmental protection. As a part of that objective, we have assembled a multi-disciplinary team of scientists, programmers, chemical analysts, and business experts who work together as a team to address complex challenges that our clients face in their operations.
            </Typography>
            <br />
            <Typography varient='paragraph'>
                If you have any questions, comments, or suggestions about our services. Please feel free to reach out to us at info@qualiteas.ca and someone from our team will get back in touch with you at the earliest.
            </Typography>
            
            </Paper>
        </Flex>
    )
}

export default AboutUS
