import React from 'react'
import { Flex } from '@chakra-ui/core'
import { Paper, Typography, makeStyles } from '@material-ui/core';


const useStyles = makeStyles({
    top_content: {
        padding: '20px',
        display: 'flex',
        alignItems: 'center'
    },
    researchers: {
        padding: '10px'
    },
    researchers_title: {
        fontSize: 'large'
    }
})

function RAndD() {
    const classes = useStyles();

    return (
        <Flex 
        width = '100%' 
        background = {`url(/images/R&D.png) no-repeat center center`}   
        backgroundSize = 'cover'
        backgroundAttachment = 'fixed'
        color = 'white'
        direction = 'column'
        >
            <Flex
            justify = 'space-around'
            margin = '50px'
            >
                <Paper square className = {classes.top_content} elevation = {5}>
                    <Typography>
                Chemical analysis of underground pipeline corrosion to unveil soil mediated corrosion mechanism
                </Typography>         
                </Paper>
                <Paper square style = {{width: '15rem', height: '3rem'}} elevation = {5}>
                    <img src = '/images/government_logo.png' alt = 'Government of Canada' style = {{width: 'inherit', height: 'inherit'}} />
                </Paper>
            </Flex>
            <Flex
            justify = 'space-around'
            margin = '60px'
            >
            <Paper square style = {{width: '15rem', height: '3rem'}} elevation = {5}>
                <img src = '/images/mitacs_colour.jpg' alt = 'Mitacs' style = {{width: 'inherit' , height: 'inherit'}} />
                </Paper>
                <Paper square className = {classes.top_content} elevation = {5}>
                    <Typography>
                Understanding Naphthenic acid corrosion in refinery sectors through optical spectroscopic methods
                </Typography>
                </Paper>
            </Flex>
            <Flex 
            justify = 'space-around'
            margin = '60px'
            >
            <Paper square className = {classes.top_content} elevation = {5}>
                <Typography align = 'center'>
            Automating Subsea Asset Image Analysis by Machine Learning for Corrosion/Pit Detection
            </Typography>
                </Paper>
                <Paper square style = {{width: '15rem' , height: '3rem'}} elevation = {5}>
                <img src = '/images/mitacs_colour.jpg' alt = 'Mitacs' style = {{width: 'inherit' , height: 'inherit'}} />
                </Paper>
            </Flex>
            <Flex 
            justify = 'space-around'
             margin = '60px'
            >
            <Paper square style = {{width: '15rem' , height: '3rem'}} elevation = {5}>
                <img src = '/images/mitacs_colour.jpg' alt = 'Mitacs' style = {{width: 'inherit',  height: 'inherit'}} />
                </Paper>
                <Paper square className = {classes.top_content} elevation = {5}>
                    <Typography>
                    3D Reconstruction of sub-sea assets using Simultaneous Localization and Mapping (SLAM)

                    </Typography>
                </Paper>
            </Flex>
            <Flex 
            direction = 'column'
            >
                <Typography color = 'white' align = 'center'>
                    Our Research Collaborators
                </Typography>

                <Flex
                justifyContent = 'space-around'
                direction = 'row'
                mt = '20px'
                wrap = 'wrap'
                >
                <Paper square className = {classes.researchers} elevation = {5}>
                   <Typography className = {classes.researchers_title} >
                   Dr. Ali Nasiri

                   </Typography>
                   <Typography>
                   Canada Research Chair

                   </Typography>
                   <Typography>
                   Dalhousie University

                   </Typography>
                   <a href = 'https://www.dal.ca/faculty/engineering/mechanical/faculty-staff/our-faculty/assistant-professors/ali-nasiri.html' target = '_blank'>Website</a>
                </Paper>
                <Paper square className = {classes.researchers } elevation = {5}>
                   <Typography className = {classes.researchers_title}>
                   Dr. Oscar Meruvia-Pastor


                   </Typography>
                   <Typography>
                   Professor
                   </Typography>
                   <Typography>
                   Memorial University

                   </Typography>
                   <a href = 'http://www.cs.mun.ca/~omeruvia/' target = '_blank'>Website</a>
                </Paper>
                <Paper square className = {classes.researchers}>
                   <Typography className = {classes.researchers_title}>
                   Dr. Stephen Czarnuch

                   </Typography>
                   <Typography>
                   Professor
                   </Typography>
                   <Typography>
                   Memorial University

                   </Typography>
                   <a href = 'http://www.engr.mun.ca/~sczarnuch/?_ga=2.253045641.1824978539.1608690974-1599330423.1602596495' target = '_blank'>Website</a>
                </Paper>
                <Paper square className = {classes.researchers} elevation = {5}>
                   <Typography className = {classes.researchers_title}>
                   Dr. Erika Merschrod

                   </Typography>
                   <Typography>
                   Professor
                   </Typography>
                   <Typography>
                   Memorial University

                   </Typography>
                   <a href = 'http://www.merschrod.group/index.html' target = '_blank'>Website</a>
                </Paper>
                </Flex>
            </Flex>
            
        </Flex>
    )
}

export default RAndD
