import React from 'react'
import {Flex, Image, List, ListItem, ListIconstItem,} from '@chakra-ui/core'
import {Typography, makeStyles, Paper} from '@material-ui/core'

const useStyle = makeStyles({
    specialty: {
        margin: '4rem',
        padding: '2rem',
        elevation: 3
    },
   serviceImage : {
    borderRadius: '60%',
    width: '50vw',
    minWidth: '200px',
    maxWidth: '500px',
    maxHeight:'281.25px',
    margin: '4rem 0',
    padding: '2rem 0',
    },
    paper:{
        padding: '2rem',
        elevation: 3
    }

   });

function Services(){

    const classes = useStyle();


    return (
        <Flex 
        width = '100%' 
        minHeight = '94vh'
        direction = 'row'
        bg = 'rgb(240,242,245)'
        >
            <Flex
            direction="column"
            height='inherit'
            justifyContent="space-around"
            >
                <Paper
                className={classes.paper}>
                    <Typography varient="paragraph" align = 'center'>
                    We are specializing in corrosion inspection and mitigation consultancy, with a proactive chemical analysis approach towards unveiling corrosion mechanism (origin, type, and degree) that is instrumental to assess the further impact(s) of corrosion on the asset and identify mitigation strategies, more accurately. This ultimately results in asset-life extension, in a cost-effective and timely manner, and operational efficiency enhancement.
                    </Typography>
                </Paper>
                <Flex 
                wrap = 'wrap-reverse'
                justify = 'center'
                >
                    <Paper className = {classes.specialty}>
                <Typography variant="h6">Our corrosion inspection services include:</Typography>
                <List styleType="decimal" as="ol" padding='1rem 0 0 0'>
                    <ListItem>Visual data analysis</ListItem>
                    <ListItem>Chemical analysis of corrosion samples that collected from the asset</ListItem>
                    <ListItem>Identifying degree and type of corrosion</ListItem>
                    <ListItem>Chemical and microbial analysis of environmental samples that causing the corrosion</ListItem>
                    <ListItem>Origin of corrosion</ListItem>
                    <ListItem>Further impact of corrosion</ListItem>
                    <ListItem>Consultation on future inspection and mitigation strategies</ListItem>
                </List>
                </Paper>
                <img src ="images/Services.png" alt="Services Image" className={classes.serviceImage}></img>
                </Flex>
                <Paper className={classes.paper}>
                <Typography align= 'center' varient="paragraph">We also offer various chemical analyses of liquid and solid samples, which involve chromatographic, microscopic and spectroscopic analyses.</Typography>
                </Paper>

            </Flex>
        </Flex>
    )
}

export default Services
