import React from 'react'
import { Flex } from "@chakra-ui/core";
import 'fontsource-roboto';
import { Typography, makeStyles } from '@material-ui/core';
import LinkedInIcon from '@material-ui/icons/LinkedIn';


const useStyles = makeStyles({
    linkedIn: {
        color: 'inherit',
        height: 'fit-content'
    }
})

function Footer() {
    const classes = useStyles();

    return (
        <Flex 
        direction = 'row' 
        justify = 'space-around'
        // borderTop = '2px solid rgb(0,128,0)'
        backgroundColor = 'rgb(31,32,34)'
        color = 'white'
        padding = '30px'
        margin = '0'
        >
            <Typography  variant = 'h6'>
               All rights reserved ©
           </Typography>
           <Typography variant = 'h6'>
               Address: 
               <p>Suite 217, Tara Place.</p>
               <p>31 Peet Street. St. John's,</p>
               <p>NL, Canada, A1B 3W8</p>
           </Typography>
           <a className = {classes.linkedIn} href = "https://www.linkedin.com/company/qualiteas-inc/" target = "_blank">
           <LinkedInIcon fontSize = 'large'/>
           </a>
        </Flex>
    )
}

export default Footer
